<script>
import HeaderArea from './components/HeaderArea.vue'
import FooterArea from './components/FooterArea.vue'


export default {
  name : 'app',
  data(){
    return{
      
    }
  },
  methods: {
    headScroll: ()=>{
      var header = document.querySelector('#headerArea');
      var article = document.querySelector('article');
      if(window.scrollY > article.offsetTop){
        header.style.background='rgba(0,0,0,.7)';
      }else{
        header.style.background='transparent';
      }
    }
  },
  components:{
       HeaderArea,
       FooterArea
  },
  mounted(){
    document.addEventListener('scroll',this.headScroll);
  }
}

</script>
