<!-- 서브 페이지 마크업 -->
<template lang="html">
    <article>
      <SubVisual />
      <h3>가까운 책방을 검색해보세요!</h3>
      <div class="searchBox">
        <input type="text" placeholder="경기도" @keyup="Bsearch" v-model="search">
        <button @click="Bsearch">검색</button>
      </div>
      <ul class="list">
        <li v-for="(item,i) in realbook" :key="i">
          <dl>
            <dt><span>{{i+1}}.</span>서점명 : {{item.FCLTY_NM}}</dt>
            <dd>주소 : {{item.FCLTY_ROAD_NM_ADDR}}</dd>
            <dd>전화번호 : {{item.TEL_NO||'전화번호없음'}}</dd>
          </dl>
        </li>
      </ul>
    </article>
  </template>
  
  <script>
  import SubVisual from './SubVisual.vue';
  export default {
    components: {
      SubVisual
    },
    data: function () {
      return {
        bookList: [],
        realbook: [],
        search: ''
      }
    },
    created: function () {
      this.getList()
    },
    methods: {
      getList: function () {
        var url = 'http://api.kcisa.kr/API_CNV_045/request'; /*URL*/
        var queryParams = '?' + encodeURIComponent('serviceKey') + '=' + '24a2c4de-6440-457e-9336-55fe524ea28d'; /*서비스키*/
        queryParams += '&' + encodeURIComponent('numOfRows') + '=' + encodeURIComponent('432'); /*세션당 요청레코드수*/
        queryParams += '&' + encodeURIComponent('pageNo') + '=' + encodeURIComponent('1'); /*페이지수*/

        // axios를 이용하여 API 호출 (component 안에서 axios를 this.$axios로 사용할 수 있습니다.
        this.$axios.get(url+queryParams).then(response => {
          console.log(url+queryParams);
          this.bookList = response.data.response.body.items.item;
          this.realbook = this.bookList;
        }).catch(error => {
          console.log(error);
        })
      },
      Bsearch: function(){
        let text = this.search;
        if(text){
          this.realbook = this.bookList.filter(function(element){
            return element.FCLTY_ROAD_NM_ADDR.includes(text)
          })
        }else{
          this.realbook = this.bookList
        }
      }
    }
  }
  </script>
  
  <style>
    article h3{
      margin: 70px 0 30px 0;
      font-size: 2em;
    }
    article .searchBox{
      margin-bottom: 30px;
    }
    article .searchBox input{
      outline: none;
      width: 300px;
      height: 30px;
      margin: 0 20px 0 0;
      padding: 0 0 0 15px;
      border: solid 1px #ccc;
      box-sizing: border-box;
    }
    article .searchBox button{
      display: inline-block;
      width: 70px;
      height: 30px;
      margin: 0;
      padding: 0;
      border: solid 1px #ccc;
      vertical-align: bottom;
      cursor: pointer;
      background: transparent;
    }
    article .list{
      text-align: start;
      width: 83.33%;
      margin: 0 auto 100px;
      border-top: solid 2px #333;
    }
    article .list li{
      border-bottom: solid 1px #333;
      padding: 10px 0;
    }
    article .list dl{
      display: flex;
      flex-direction: column;
      position: relative;
    }
    article .list dt{
      font-weight: 700;
      font-size: 1.25em;
      color: #333;
    }
    article .list dt span{
      font-weight: 400;
      display: inline-block;
      margin-right: 15px;
    }
    article .list dd{
      margin-top: 20px;
    }
    article .list dd:last-child{
      position: absolute;
      right: 0; bottom: 0;
      color: green;
    }
    @media screen and (max-width:768px){
      article .searchBox input{
        margin: 0;
      }
      article .list dd:last-child{
        position: static;
      }
    }
  </style>