<!-- 서브 페이지 마크업 -->
<template lang="html">
  <article>
    <SubVisual2 />
    <h3>책크방에서 추천드리는 추천도서입니다.</h3>
    <ul class="BookList">
      <li  v-for="(item,i) in List" :key="i">
        <img :src="require('../assets/list-'+(i+1)+'.png')">
        <dl>
          <dt>{{item.dt}}</dt>
          <dd><span>{{item.span}}</span>{{item.dd}}</dd>
        </dl>
      </li>
    </ul>
  </article>
</template>
  
  <script>
  import SubVisual2 from './SubVisual2.vue';
  import bookJson from '../assets/book.json';

  export default {
    components: {
      SubVisual2
    },
    data(){
      return{
        List: bookJson
      }
    }
  }
  </script>
  
  <style>
     article h3{
      margin-bottom: 50px;
     }
     article .BookList{
      width: 83.33%;
      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;
     }
     article .BookList li{
      width: 30%;
      margin: 0 5% 50px 0;
      box-sizing: border-box;
      border: solid 1px #ccc;
     }
     article .BookList li:nth-child(3n){
      margin-right: 0;
     }
     article .BookList li img{
      width: 100%;
     }
     article .BookList li dl{
      border-top: solid 1px #333;
      padding: 15px;
     }
     article .BookList li dt{
      text-align: start;
      margin-bottom: 15px;
     }
     article .BookList li dd{
      text-align: justify;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 8;
      overflow: hidden;
      text-overflow: ellipsis;
     }
     @media  screen and (max-width:1200px){
      article .BookList{
        justify-content: space-between;
      }
      article .BookList li{
        width: 45%;
        margin: 0 0 50px 0;
      }
      article .BookList li:nth-child(3n){
        margin-right: 0;
      }
     }
     @media  screen and (max-width:768px){
      article .BookList li{
        width: 100%;
        margin: 0 0 50px 0;
      }
     }
  </style>